import React, { useState, useEffect } from "react";
import axios from "axios";
import { Gallery } from "react-grid-gallery";
import EXIF from "exif-js";
import { Card, Button, Row, Col } from "react-bootstrap";
import "./YourImages.css"; // Import CSS file

function YourImages() {
  const [allImage, setAllImage] = useState(null);
  const [images, setImages] = useState([]);
  const [selectedImages, setSelectedImages] = useState([]);
  const [hasSelected, setHasSelected] = useState(false);
  const [isSelecting, setIsSelecting] = useState(false);

  useEffect(() => {
    getImage();
  }, []);

  const getImage = async () => {
    try {
      const result = await axios.get("http://localhost:8080/get-image");
      const mappedImages = await Promise.all(result.data.data.map(async (image) => {
        const imgSrc = require(`./images/${image.image}`);
        const exifData = await getExifData(imgSrc);
        return {
          src: imgSrc,
          thumbnail: imgSrc,
          thumbnailWidth: 200,
          thumbnailHeight: 200,
          isSelected: false,
          _id: image._id, // Store the image ID for deletion
          caption: image.image,
          exif: exifData
        };
      }));
      setImages(mappedImages);
      setAllImage(result.data.data);
    } catch (error) {
      console.error("Error fetching images:", error);
    }
  };

  const getExifData = (imgSrc) => {
    return new Promise((resolve) => {
      const img = new Image();
      img.onload = () => {
        EXIF.getData(img, function () {
          const longitude = EXIF.getTag(this, "GPSLongitude");
          const latitude = EXIF.getTag(this, "GPSLatitude");
          resolve({ longitude, latitude });
        });
      };
      img.src = imgSrc;
    });
  };

  const saveLocationToAPI = async (latitude, longitude, name) => {
    const toDecimal = (gpsData) => {
      if (!gpsData) return null;
      const [degrees, minutes, seconds] = gpsData;
      return degrees + minutes / 60 + seconds / 3600;
    };

    const latitudeDecimal = toDecimal(latitude);
    const longitudeDecimal = toDecimal(longitude);

    try {
      await axios.post("http://localhost:8080/locations", {
        name,
        latitude: latitudeDecimal,
        longitude: longitudeDecimal,
      });
      alert(`Location for ${name} uploaded successfully!`);
    } catch (error) {
      console.error("Error saving location to API:", error);
    }
  };

  const handleSelect = (index) => {
    const nextImages = images.map((image, i) =>
      i === index ? { ...image, isSelected: !image.isSelected } : image
    );
    setImages(nextImages);
    setSelectedImages(nextImages.filter(image => image.isSelected));
    setHasSelected(nextImages.some((image) => image.isSelected));
  };

  const handleDeleteSelected = async () => {
    const deletePromises = selectedImages.map(image =>
      axios.delete(`http://localhost:8080/delete-image/${image._id}`)
    );

    try {
      await Promise.all(deletePromises);
      getImage(); // Refresh the images after deletion
      setHasSelected(false);
      setSelectedImages([]);
    } catch (error) {
      console.error("Error deleting images:", error);
    }
  };

  const toggleSelection = () => {
    setIsSelecting(!isSelecting);
    setHasSelected(false); // Deselect all images when toggling selection mode
    setSelectedImages([]);
    const resetImages = images.map((image) => ({ ...image, isSelected: false }));
    setImages(resetImages);
  };

  return (
    <div>
      <Button onClick={toggleSelection} variant="primary">
        {isSelecting ? 'Cancel Selection' : 'Select Images'}
      </Button>
      {isSelecting && hasSelected && (
        <Button onClick={handleDeleteSelected} variant="danger">
          Delete Selected
        </Button>
      )}
      {/* <Gallery images={images} onSelect={handleSelect} /> */}
      <Row xs={1} md={2} lg={3} xl={4} className="g-4">
        {images.map((image, index) => (
          <Col key={index}>
            <Card style={{ width: '18rem', margin: '80px' }}>
              <Card.Img variant="top" src={image.src} />
              <Card.Body>
                <Card.Title>{image.caption}</Card.Title>
                <div>
                  <strong>Longitude:</strong> {image.exif?.longitude ? image.exif.longitude.join(', ') : 'N/A'}
                  <br />
                  <strong>Latitude:</strong> {image.exif?.latitude ? image.exif.latitude.join(', ') : 'N/A'}
                </div>
                {isSelecting && (
                  <Button
                    variant={image.isSelected ? "warning" : "primary"}
                    onClick={() => handleSelect(index)}
                  >
                    {image.isSelected ? "Deselect" : "Select"}
                  </Button>
                )}
                {!isSelecting && (
                  <Button
                    variant="primary"
                    onClick={() => saveLocationToAPI(image.exif.latitude, image.exif.longitude, image.caption)}
                    disabled={!image.exif?.latitude || !image.exif?.longitude}
                  >
                    Upload to Map
                  </Button>
                )}
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>
    </div>
  );
}

export default YourImages;
