import React, { useState, useRef, useEffect } from 'react';
import ImgsViewer from 'react-images-viewer';
import axios from 'axios';
import ExifReader from 'exif-js';
import Modal from 'react-modal';
import './Home.css';

Modal.setAppElement('#root'); // Adjust according to your app structure

const HomeWrapper = () => {
  const fileInputRef = useRef(null);
  const [selectedImages, setSelectedImages] = useState([]);
  const [viewImages, setViewImages] = useState([]);
  const [isOpen, setIsOpen] = useState(false);
  const [currImg, setCurrImg] = useState(0);
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [modalImageDetails, setModalImageDetails] = useState(null);
  const [image, setImage] = useState(null);
  const [allImage, setAllImage] = useState(null);

  useEffect(() => {
    getImage();
  }, []);

  const handleFileChange = async (event) => {
    const files = event.target.files;
    const selected = [...selectedImages]; // Copy the existing array of selected images
    for (let i = 0; i < files.length; i++) {
      const imageInfo = await processImage(files[i]);
      selected.push(imageInfo);
    }
    setSelectedImages(selected);
  };

  const uploadImage = async () => {
    if (!image) {
      alert("Please select an image first.");
      return;
    }

    const formData = new FormData();
    formData.append("image", image);

    try {
      const result = await axios.post(
        "http://localhost:8080/upload-image",
        formData,
        {
          headers: { "Content-Type": "multipart/form-data" },
        }
      );
      console.log(result.data);
      if (result.data.status === "ok") {
        alert("Image uploaded successfully");
        getImage();
      } else {
        alert("Failed to upload image");
      }
    } catch (error) {
      console.error("Error uploading image:", error);
      alert("An error occurred while uploading the image.");
    }
  };

  const processImage = (file) => {
    return new Promise((resolve) => {
      const reader = new FileReader();
      reader.onload = (event) => {
        const img = new Image();
        img.onload = () => {
          // Extract EXIF data
          ExifReader.getData(img, function () {
            const allMetaData = ExifReader.getAllTags(this);

            const resolution = `${img.width}x${img.height}`;
            const size = `${(file.size / 1024).toFixed(2)} KB`;
            const type = file.type;
            const fileName = file.name;
            const fileType = file.type;
            const make = allMetaData.Make || '---';
            const model = allMetaData.Model || '---';
            const aperture = allMetaData.FNumber ? `f/${allMetaData.FNumber}` : '---';
            const focalLength = allMetaData.FocalLength ? `${allMetaData.FocalLength} mm` : '---';
            const shutterSpeed = allMetaData.ExposureTime ? `${allMetaData.ExposureTime} sec` : '---';
            const isoSpeed = allMetaData.ISOSpeedRatings || '---';
            const dateTime = allMetaData.DateTimeOriginal || '---';
            const gpsLatitude = allMetaData.GPSLatitude ? `${allMetaData.GPSLatitude.join(', ')} ${allMetaData.GPSLatitudeRef}` : '---';
            const gpsLongitude = allMetaData.GPSLongitude ? `${allMetaData.GPSLongitude.join(', ')} ${allMetaData.GPSLongitudeRef}` : '---';

            // Resolve with the original image URL and additional information
            resolve({
              src: event.target.result,
              resolution,
              size,
              type,
              fileName,
              fileType,
              make,
              model,
              aperture,
              focalLength,
              shutterSpeed,
              isoSpeed,
              dateTime,
              gpsLatitude,
              gpsLongitude
            });
          });
        };
        img.src = event.target.result;
      };
      reader.readAsDataURL(file);
    });
  };

  const handleImageClick = (index) => {
    setCurrImg(index);
    setIsOpen(true);
  };

  const handleAddImage = () => {
    fileInputRef.current.click(); // Trigger file input when "+" button is clicked
  };

  const handleDone = async () => {
    setIsOpen(false);
  };

  const handleCloseImgsViewer = () => {
    setIsOpen(false);
  };

  const dataURItoBlob = (dataURI) => {
    const byteString = atob(dataURI.split(',')[1]);
    const mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];
    const ab = new ArrayBuffer(byteString.length);
    const ia = new Uint8Array(ab);
    for (let i = 0; i < byteString.length; i++) {
      ia[i] = byteString.charCodeAt(i);
    }
    return new Blob([ab], { type: mimeString });
  };

  const openModal = (imageDetails) => {
    setModalImageDetails(imageDetails);
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
    setModalImageDetails(null);
  };

  const getImage = async () => {
    try {
      const result = await axios.get("http://localhost:8080/get-image");
      console.log(result);
      setAllImage(result.data.data);
    } catch (error) {
      console.error("Error fetching images:", error);
    }
  };

  return (
    <div className="home-wrapper">
      <h1>IMPORT YOUR PHOTOS</h1>
      <p>Make sure they are JPG or HEIF format</p>
      {/* Hidden file input */}
      <input
        ref={fileInputRef}
        type="file"
        accept=".jpg,.heif"
        style={{ display: 'none' }}
        onChange={(e) => {
          handleFileChange(e);
          setImage(e.target.files[0]);
        }}
        multiple
      />
      {/* Display selected images */}
      <div className="selected-images">
        {selectedImages.map((image, index) => (
          <div key={index} className="image-container" onMouseEnter={() => setViewImages(selectedImages.map(img => ({ src: img.src })))}>
            <img src={image.src} alt={`Selected Image ${index}`} />
            <div className="image-details">
              <p>Resolution: {image.resolution}</p>
              <p>Size: {image.size}</p>
              <p>Type: {image.type}</p>
              <button onClick={() => openModal(image)} style={{ background: 'linear-gradient(to right, #28701a, #6c4f21)', color: 'black', borderColor: '#ffa500', backgroundSize: '200% 100%', backgroundPosition: '-100% 0' }}>View Details</button>
            </div>
          </div>
        ))}
        {/* "+" button */}
        <button className="add-image-button" onClick={handleAddImage}>
          Add image
        </button>
      </div>
      {/* Done button (conditionally rendered) */}
      {selectedImages.length > 0 && (
        <button className="done-button" onClick={uploadImage}>
          Done
        </button>
      )}
      {/* View images */}
      <ImgsViewer
        imgs={viewImages}
        isOpen={isOpen}
        currImg={currImg}
        onClickPrev={() => setCurrImg(currImg - 1)}
        onClickNext={() => setCurrImg(currImg + 1)}
        onClose={handleCloseImgsViewer}
      />
      {/* Modal for detailed information */}
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        contentLabel="Image Details"
        style={{
          overlay: {
            backgroundColor: 'rgba(0, 0, 0, 0.5)', // Semi-transparent black overlay
          },
          content: {
            backgroundColor: 'rgba(0, 0, 0, 0.5)', // White modal background
            padding: '20px',
            maxWidth: '80%',
            margin: '0 auto',
            color: '#ffffff'
          }
        }}
      >
        {modalImageDetails && (
          <div className="modal-content">
            <button className="close-button" onClick={closeModal}></button>
            <h2>Image Details</h2>
            <img src={modalImageDetails.src} alt="Detailed View" style={{ maxWidth: '100%' }} />
            <p>Resolution: {modalImageDetails.resolution}</p>
            <p>Size: {modalImageDetails.size}</p>
            <p>Type: {modalImageDetails.type}</p>
            <p>File Name: {modalImageDetails.fileName}</p>
            <p>File Type: {modalImageDetails.fileType}</p>
            <p>Device: {modalImageDetails.make} {modalImageDetails.model}</p>
            <p>Aperture: {modalImageDetails.aperture}</p>
            <p>Focal Length: {modalImageDetails.focalLength}</p>
            <p>Shutter Speed: {modalImageDetails.shutterSpeed}</p>
            <p>ISO Speed: {modalImageDetails.isoSpeed}</p>
            <p>Date Time: {modalImageDetails.dateTime}</p>
            <p>GPS Latitude: {modalImageDetails.gpsLatitude}</p>
            <p>GPS Longitude: {modalImageDetails.gpsLongitude}</p>
          </div>
        )}
      </Modal>
    </div>
  );
};

export default HomeWrapper;
